import { AuthenticationSession, authentication, window } from 'vscode';
import { Agent, globalAgent } from 'https';
import { graphql } from '@octokit/graphql/dist-types/types';
import { Octokit } from '@octokit/rest';
import { httpsOverHttp } from 'tunnel';
import { URL } from 'url';

export class $a extends Error { }

function getAgent(url: string | undefined = process.env.HTTPS_PROXY): Agent {
	if (!url) {
		return globalAgent;
	}

	try {
		const { hostname, port, username, password } = new URL(url);
		const auth = username && password && `${username}:${password}`;
		return httpsOverHttp({ proxy: { host: hostname, port, proxyAuth: auth } });
	} catch (e) {
		window.showErrorMessage(`HTTPS_PROXY environment variable ignored: ${e.message}`);
		return globalAgent;
	}
}

const scopes = ['repo', 'workflow', 'user:email', 'read:user'];

export async function $b(): Promise<AuthenticationSession> {
	let attempt = 1;
	while (true) {
		const session = await authentication.getSession('github', scopes, { createIfNone: true });
		if (session !== undefined || ++attempt > 5) {
			return session;
		}
		console.error("github: Could not generate auth session, retrying in 1 second...");
		await new Promise(resolve => setTimeout(resolve, 1000));
	}
}

let _octokit: Promise<Octokit> | undefined;

export function $c(): Promise<Octokit> {
	if (!_octokit) {
		_octokit = $b().then(async session => {
			const token = session.accessToken;
			const agent = getAgent();

			const { Octokit } = await import('@octokit/rest');

			return new Octokit({
				request: { agent },
				userAgent: 'GitHub VSCode',
				auth: `token ${token}`
			});
		}).then(null, async err => {
			_octokit = undefined;
			throw err;
		});
	}

	return _octokit;
}

let _octokitGraphql: Promise<graphql> | undefined;

export async function $d(): Promise<graphql> {
	if (!_octokitGraphql) {
		try {
			const session = await authentication.getSession('github', scopes, { silent: true });

			if (!session) {
				throw new $a('No GitHub authentication session available.');
			}

			const token = session.accessToken;
			const { graphql } = await import('@octokit/graphql');

			return graphql.defaults({
				headers: {
					authorization: `token ${token}`
				},
				request: {
					agent: getAgent()
				}
			});
		} catch (err) {
			_octokitGraphql = undefined;
			throw err;
		}
	}

	return _octokitGraphql;
}
