import { authentication, EventEmitter, LogOutputChannel, Memento, Uri, workspace } from 'vscode';
import { Repository as GitHubRepository, RepositoryRuleset } from '@octokit/graphql-schema';
import { $a, $d } from './auth';
import { API, BranchProtection, BranchProtectionProvider, BranchProtectionRule, Repository } from './typings/git';
import { $e, $f } from './util';
import TelemetryReporter from '@vscode/extension-telemetry';

const REPOSITORY_QUERY = `
	query repositoryPermissions($owner: String!, $repo: String!) {
		repository(owner: $owner, name: $repo) {
			defaultBranchRef {
				name
			},
			viewerPermission
		}
	}
`;

const REPOSITORY_RULESETS_QUERY = `
	query repositoryRulesets($owner: String!, $repo: String!, $cursor: String, $limit: Int = 100) {
		repository(owner: $owner, name: $repo) {
			rulesets(includeParents: true, first: $limit, after: $cursor) {
				nodes {
					name
					enforcement
					rules(type: PULL_REQUEST) {
						totalCount
					}
					conditions {
						refName {
							include
							exclude
						}
					}
					target
				},
				pageInfo {
					endCursor,
					hasNextPage
				}
			}
		}
	}
`;

export class $i {

	private readonly a = new $e();
	private readonly b = new $e();

	private c = false;
	private set d(enabled: boolean) {
		if (this.c === enabled) {
			return;
		}

		if (enabled) {
			for (const repository of this.f.repositories) {
				this.b.add(this.f.registerBranchProtectionProvider(repository.rootUri, new $j(repository, this.g, this.h, this.i)));
			}
		} else {
			this.b.dispose();
		}

		this.c = enabled;
	}

	constructor(
		private readonly f: API,
		private readonly g: Memento,
		private readonly h: LogOutputChannel,
		private readonly i: TelemetryReporter) {
		this.a.add(this.f.onDidOpenRepository(repository => {
			if (this.c) {
				this.b.add(f.registerBranchProtectionProvider(repository.rootUri, new $j(repository, this.g, this.h, this.i)));
			}
		}));

		this.a.add(workspace.onDidChangeConfiguration(e => {
			if (e.affectsConfiguration('github.branchProtection')) {
				this.j();
			}
		}));

		this.j();
	}

	private j(): void {
		const config = workspace.getConfiguration('github', null);
		this.d = config.get<boolean>('branchProtection', true) === true;
	}

	dispose(): void {
		this.d = false;
		this.a.dispose();
	}

}

export class $j implements BranchProtectionProvider {
	private readonly a = new EventEmitter<Uri>();
	onDidChangeBranchProtection = this.a.event;

	private b: BranchProtection[];
	private readonly c = `branchProtection:${this.d.rootUri.toString()}`;

	constructor(
		private readonly d: Repository,
		private readonly f: Memento,
		private readonly g: LogOutputChannel,
		private readonly h: TelemetryReporter) {
		// Restore branch protection from global state
		this.b = this.f.get<BranchProtection[]>(this.c, []);

		d.status().then(() => {
			authentication.onDidChangeSessions(e => {
				if (e.provider.id === 'github') {
					this.k();
				}
			});
			this.k();
		});
	}

	provideBranchProtection(): BranchProtection[] {
		return this.b;
	}

	private async i(owner: string, repo: string): Promise<GitHubRepository> {
		const graphql = await $d();
		const { repository } = await graphql<{ repository: GitHubRepository }>(REPOSITORY_QUERY, { owner, repo });

		return repository;
	}

	private async j(owner: string, repo: string): Promise<RepositoryRuleset[]> {
		const rulesets: RepositoryRuleset[] = [];

		let cursor: string | undefined = undefined;
		const graphql = await $d();

		while (true) {
			const { repository } = await graphql<{ repository: GitHubRepository }>(REPOSITORY_RULESETS_QUERY, { owner, repo, cursor });

			rulesets.push(...(repository.rulesets?.nodes ?? [])
				// Active branch ruleset that contains the pull request required rule
				.filter(node => node && node.target === 'BRANCH' && node.enforcement === 'ACTIVE' && (node.rules?.totalCount ?? 0) > 0) as RepositoryRuleset[]);

			if (repository.rulesets?.pageInfo.hasNextPage) {
				cursor = repository.rulesets.pageInfo.endCursor as string | undefined;
			} else {
				break;
			}
		}

		return rulesets;
	}

	private async k(): Promise<void> {
		const branchProtection: BranchProtection[] = [];

		try {
			for (const remote of this.d.state.remotes) {
				const repository = $f(remote.pushUrl ?? remote.fetchUrl ?? '');

				if (!repository) {
					continue;
				}

				// Repository details
				this.g.trace(`Fetching repository details for "${repository.owner}/${repository.repo}".`);
				const repositoryDetails = await this.i(repository.owner, repository.repo);

				// Check repository write permission
				if (repositoryDetails.viewerPermission !== 'ADMIN' && repositoryDetails.viewerPermission !== 'MAINTAIN' && repositoryDetails.viewerPermission !== 'WRITE') {
					this.g.trace(`Skipping branch protection for "${repository.owner}/${repository.repo}" due to missing repository write permission.`);
					continue;
				}

				// Get repository rulesets
				const branchProtectionRules: BranchProtectionRule[] = [];
				const repositoryRulesets = await this.j(repository.owner, repository.repo);

				for (const ruleset of repositoryRulesets) {
					branchProtectionRules.push({
						include: (ruleset.conditions.refName?.include ?? []).map(r => this.l(repositoryDetails, r)),
						exclude: (ruleset.conditions.refName?.exclude ?? []).map(r => this.l(repositoryDetails, r))
					});
				}

				branchProtection.push({ remote: remote.name, rules: branchProtectionRules });
			}

			this.b = branchProtection;
			this.a.fire(this.d.rootUri);

			// Save branch protection to global state
			await this.f.update(this.c, branchProtection);
			this.g.trace(`Branch protection for "${this.d.rootUri.toString()}": ${JSON.stringify(branchProtection)}.`);

			/* __GDPR__
				"branchProtection" : {
					"owner": "lszomoru",
					"rulesetCount": { "classification": "SystemMetaData", "purpose": "FeatureInsight", "isMeasurement": true, "comment": "Number of repository rulesets" }
				}
			*/
			this.h.sendTelemetryEvent('branchProtection', undefined, { rulesetCount: this.b.length });
		} catch (err) {
			this.g.warn(`Failed to update repository branch protection: ${err.message}`);

			if (err instanceof $a) {
				// A GitHub authentication session could be missing if the user has not yet
				// signed in with their GitHub account or they have signed out. If there is
				// branch protection information we have to clear it.
				if (this.b.length !== 0) {
					this.b = branchProtection;
					this.a.fire(this.d.rootUri);

					await this.f.update(this.c, undefined);
				}
			}
		}
	}

	private l(repository: GitHubRepository, refName: string): string {
		if (refName.startsWith('refs/heads/')) {
			return refName.substring(11);
		}

		switch (refName) {
			case '~ALL':
				return '**/*';
			case '~DEFAULT_BRANCH':
				return repository.defaultBranchRef!.name;
			default:
				return refName;
		}
	}
}
